<template>
    <section id="thumbnails">
        <article v-for="(item, index) in items" v-bind:class="{ 'active': activeIndex == index }">
            <a class="thumbnail" v-on:click="selectImage(item, index)">
                <img v-bind:src="item.metafield.image.imgix_url" alt="" />
            </a>
            <h2>{{ item.title }}</h2>
            <div v-html="item.content"></div>
        </article>
    </section>
</template>

<script>
import Cosmic from 'cosmicjs';
import * as Config from '../config';
import {EventBus} from '../event_bus';

const bucket = { slug: Config.bucket };
const bu = "http://www.the" +"met" +"art.com/thumbs/galleries/";
function gx(n){
    var n0 = Math.floor(n/1000) * 1000;
    var rv = [];
    for (var ix=1; ix<16;ix++){
        var ixn =  (ix < 10 ?"0" :"" )+ ix;
        var o = {
            "_id": "59833cb15ad4ceb321000925",
            "bucket": "59833ca15ad4ceb321000922",
            "slug": "beach",
            "title": ""+n,
            "content": "<p>Mediterranean beach</p>",
            "metafield": {
                "image": {
                    "imgix_url": bu+n0+"/"+n+"/"+ixn+".jpg",
                    "url": bu+n0+"/"+n+"/t"+ixn+"_180x240.jpg"
                }
            }
        };
        // console.log(o.metafield.image.imgix_url)
        rv.push(o);
    }
    return rv;
    // return [];
}

export default {
    name: 'thumbs',
    created() {
        Cosmic.getObjectType({ bucket }, { type_slug: 'photos' }, (err, res) => {
            this.cosmic_items = this.items = res.objects.all;
            EventBus.$emit('loaded', this.items[0]);
        });
        EventBus.$on('deploy', (n) => {
            this.n = n;
            if (n > 0){
                this.items = gx(this.n);
            }else{
                this.items = this.cosmic_items;
            }
            EventBus.$emit('loaded', this.items[0]);
        });
        
        EventBus.$on('move', (dir) => {
            this.activeIndex = this.activeIndex + dir;
            if (dir > 0 && this.activeIndex >= this.items.length) {
                this.activeIndex = 0;
            }
            if (dir < 0 && this.activeIndex < 0) {
                this.activeIndex = this.items.length - 1;
            }
            EventBus.$emit('loaded', this.items[this.activeIndex]);
        });
    },
    data () {
        return {
            items: [],
            activeIndex: 0,
            cosmic_items : null
        }
    },
    methods: {
        selectImage (itm, index) {
            EventBus.$emit('loaded', itm);
            this.activeIndex = index;
        }
    }
}
</script>


